.conservacion {
  background-color: #FED2AA;
  color: #E67E22;
}

.categoria-producto {
  background-color: #448EF6;
  border-radius: 50px 0px 0px 50px;
}
.categoria-producto p {
  font-size: 2.5rem;
  color: #FEF9E7;
  margin-right: 10rem;
  margin-left: 1rem;
}

.contenedor-listProductos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contenedor-listProductos {
    flex-direction: row;
    align-items: initial;
  }
}

.cont-listadoProductos {
  background: #F4F6F6;
  box-shadow: inset 20px -20px 60px #cfd1d1, inset -20px 20px 60px #ffffff;
}

.filaProductos {
  display: grid;
  gap: 1;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 1024px) {
  .filaProductos {
    display: grid;
    gap: 1;
    grid-template-columns: repeat(4, 1fr);
  }
}

.cont-filtros {
  display: block;
  width: 70rem;
  margin-right: 2rem;
}

.titulo-filtro {
  border-bottom: 3px solid #fdbb2d;
}
.titulo-filtro h3 {
  font-size: 2.5rem;
  color: #448EF6;
}

.filtro {
  background-color: #FEF9E7;
  border-bottom: 3px solid #448EF6;
  display: block;
  margin-top: 2rem;
}
.filtro:hover {
  cursor: pointer;
}

.icono-arrowDown {
  display: flex;
}

.icono-arrowUp {
  display: none;
}

.despliege {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 1rem;
}
.despliege h3 {
  font-size: 2rem;
  font-weight: 700;
}
.despliege img {
  height: 3rem;
  width: 5rem;
  padding: 0;
  margin: 0;
}

.contenido-filtro {
  display: none;
}
.contenido-filtro ul {
  list-style: none;
  margin: 0;
}
.contenido-filtro li {
  margin: 0;
}

.mostrar {
  display: flex;
}

.quitar {
  display: none;
}

.alerta {
  color: white;
  font-weight: bold;
  text-align: center;
  border-radius: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.error {
  background-color: #C0392B;
}

.correcto {
  background-color: #27AE60;
}

.advertencia {
  background-color: #FF8E00;
  font-weight: bold;
  text-align: center;
  border-radius: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}
.advertencia p {
  color: white;
  max-width: 90rem;
  font-size: 1.5rem;
}

table.tabla-productos {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.tabla-productos thead {
  background-color: #FED1EF;
}
table.tabla-productos thead th {
  padding: 1.5rem;
}
table.tabla-productos .imagen-tabla {
  width: 10rem;
}
table.tabla-productos tbody {
  background-color: #FEF9E7;
}
table.tabla-productos tbody th {
  border-bottom: 2px solid #448EF6;
}

.imagen-small {
  width: 25rem;
}